body,
html {
  align-items: center;
  background-color: #29323C;
  display: flex;
  font-family: "Roboto";
  font-size: 10px;
  height: 100%;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 100%;
}

* {
  box-sizing: border-box;
}

h1 {
  color: #fff;
  font-size: 25px;
  border-bottom: 10px solid #ff8d00;
}

#round {
  position: relative;
  display: inline-block;
  white-space: nowrap;
  border: 0px outset blue;
  font-size: 3em;
  text-align: center;
  height: 0px;
  width: 0px;
  padding: 50px;
  top: -30%;
  left: -15%;
  border-radius: 100%;
}

#round .char {
  color: #fff;
  position: absolute;
  margin: 50%;
  padding: auto;
  display: inline-block;
  transform-origin: 50% 100%;
  border: 0px solid red;
}

.card {
  width: 150px;
  height: 150px;
  position: relative;
  z-index: 2;
  border-radius: 100px;
  top: -30%;
  left: -10%;
}
.card .content {
  width: 100%;
  height: 100%;
  background-color: #0c1016;
  border-radius: 95px;
}
.card::before, .card::after {
  content: "";
  width: 160px;
  height: 160px;
  display: block;
  position: absolute;
  z-index: -1;
  border-radius: 100px;
  background-image: linear-gradient(45deg, #ff00aa, #00FFF1);
  background-size: 100% 300%;
  background-position: center center;
  top: -5px;
  left: -5px;
  animation: spin 3000ms infinite alternate;
}
.card::after {
  filter: blur(75px);
}

@keyframes spin {
  0% {
    background-position: top center;
  }
  100% {
    background-position: bottom center;
  }
}
#warped {
  position: relative;
  display: block;
  width: 846px;
  height: 440px;
  color: #ff8d00;
  text-shadow: 0px 15px 10px #000, 0px 3px 4px #fff;
}

#warped > span[class^=w]:nth-of-type(n+0) {
  display: block;
  position: absolute;
  -moz-transform-origin: 50% 100%;
  -webkit-transform-origin: 50% 100%;
  -o-transform-origin: 50% 100%;
  -ms-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
}

#warped span {
  font-family: "ABeeZee";
  font-size: 57px;
  font-weight: regular;
  font-style: normal;
  line-height: 1.7;
  white-space: pre;
  overflow: visible;
  padding: 0px;
}

